@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background-color: black;
    font-family: 'Roboto', sans-serif;
}

a{
    text-decoration: none;
    color: inherit;
}

.header{
    display: flex;
    justify-content: space-evenly;
    background-color: rgb(0, 220, 100);
    padding: 15px 0px;
    color: white;
    width: 100%;
}

@media(max-width:768px){
    .header{
        display: none;
    }
}
    
#myBtn{
    background-color: rgb(0, 220, 100);
    width: 100%;
    color: white;
    font-size: 30px;
    display: none;
}

@media(max-width:768px){
#myBtn{
    display: inline;
    }
}

.modal {
    position: fixed; 
    z-index: 1; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    background-color: rgba(0,0,0,0.4); 
    display: none;
    opacity: 0; 
    animation: aparecer 500ms ease forwards; 
}
    
.modal-content {
    background-color: rgb(0, 220, 100);
    color: white;
    margin: 15% auto; 
    padding: 20px;
    border: 1px solid #888;
    width: 70%; 
    height: 40%;
    box-shadow: 0px 0px 10px white;
    font-size: 25px;
    font-weight: 700;
    border: 4px solid white;
    opacity: 0; 
    animation: aparecer 500ms ease forwards;
}
    
.close {
    color: white;
    text-shadow: 0px 0px 5px black;
    float: right;
    font-size: 30px;
    font-weight: bold;
}
    
.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.containerHome{
    text-align: center;
    margin: 35px auto;
    width: 700px;
    font-size: 17px;
    line-height: 40px;
    opacity: 0; 
    animation: aparecer 3s ease forwards;
}

@media(max-width:768px){
    .containerHome{
        width: 350px;
        font-size: 10px;
    }
}

