.but{
    font-size: 15px;
    border: 2px solid rgb(0, 220, 100);
    color: white;
    background-color: black;
    cursor: pointer;
    margin: 20px 0px;
    border-radius: 5px;
    width: 180px;
    height: 60px;
    box-shadow: 0px 0px 15px rgb(0, 220, 100);
}

@keyframes aparecer {
    from {
      opacity: 0; 
    }
    to {
      opacity: 1;
    }
}

.fade-in {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}
  
.fade-out {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}
